<template>
  <div id="lk">
    <div id="home">
      <div class="card" v-infinite-scroll="load"
        infinite-scroll-distance="0"
        infinite-scroll-delay="2000"
        infinite-scroll-immediate="false"
        infinite-scroll-disabled="disabled" v-for="(i,index) in goodsdata"
        @click="xq(i.orderid)">
        <img :src="JSON.parse(i.images)[0]"/>
        <p>{{i.name}}</p>
        <p>{{i.price}}</p>
      </div>
      <div>
        <p v-if="loading">加载中...</p>
        <p v-if="noMore">没有更多了</p>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        loading: false,
        goodsdata:[],
        page:1,
        lenpage:null,

      }
    },
    methods:{
      xq(orderid){
        this.$router.push({name:'details',query:{orderid:orderid}})
      },
      // this.$router.push({name:'xiangqing',query:{param:name}})
      getgoo(){
          this.$api.getgoods({page:this.page}).then(res=>{
            this.goodsdata.push(...(res.data.data));
            this.lenpage= Math.ceil(res.data.total/6);
            // sessionStorage.setItem('homedata',JSON.stringify(this.goodsdata));
            // this.page=Math.ceil(JSON.parse(sessionStorage.getItem('homedata')).length/6);
            // this.goodsdata.push(...(res.data.data))
            // console.log(this.goodsdata);
            // sessionStorage.setItem('goods',JSON.stringify(this.goodsdata));
            // console.log(JSON.parse(sessionStorage.getItem('goods')));
          })
      },
      load () {
          this.loading = true
          setTimeout(() => {
            this.page++
            this.getgoo()
            this.loading = false
          }, 2000)
      }
    },
    mounted(){
      // if(sessionStorage.getItem('homedata')){
      //   this.goodsdata=JSON.parse(sessionStorage.getItem('homedata'));
      // }else{
        this.getgoo();
      // }
    },
    computed:{
      noMore () {
          return this.page >= this.lenpage
      },
      disabled () {
          return this.loading || this.noMore
        }
    },
    created(){
    //   if(sessionStorage.getItem('homedata')){
    //     this.page=Math.ceil(JSON.parse(sessionStorage.getItem('homedata')).length/6);
    //     this.$api.getgoods().then(res=>{
    //       this.lenpage= Math.ceil(res.data.total/6);
    //     })
    //   }
    }
  }
</script>
