  p{
    font-size: 16px;
  }
  img{
    width: 100%;height: 250px;object-fit: cover;border-radius: 10px 10px 0 0;
  }
    #lk{
      height: 100%;
    }
    .card:hover{
      box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
      cursor:pointer
    }
    #home{
      height: 100%;
      display: flex;
      // display: grid;
      flex-wrap: wrap;
      // justify-content: center;
      // justify-content: space-between;
      padding: 10px;
    }
    .card{
      --n:4;
      // --gap: calc((100% - 200px * var(--n)) / var(--n) /2);
      --gap: calc((100% - 200px * var(--n)) / var(--n) /2);
      width: 200px;
      height: 300px;
      border-radius: 10px;
      background: rgb(255, 255, 255);
      margin: 10px var(--gap);
